
<template>
  <div class="space">
    <condition-frame class="op-query">
      <!--表单布局 主要是为了条件区域进行查询-->
        <el-form  :inline="true" ref="formData" label-width="0">
          <el-form-item>
            <span>当前共有<span class="space-parkingCount">{{parkingCount}}</span>家停车场</span>
          </el-form-item>
          <el-form-item  prop="selectedRegions">
                  <el-cascader
                    class="sidebar-search"
                    v-loading.body="regionLoading"
                    v-model="selectedRegions"
                    :options="cityList"
                    change-on-select
                    @change="regionChange"
                    :props="props"
                    placeholder="请选择-省/市/区"
                  ></el-cascader> 
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="fetchData" size="small">搜索</el-button>                
              </el-form-item>                
        </el-form>
    </condition-frame> 
    <div class="container-fluid">
      <div class="row">
        <div class="parkingSpace col-lg-3 col-md-3 col-sm-3 col-xs-3">
          <div class="parkingSpace-box">
            <h4 class="parking-name">观山湖区会展东路市政道路停车场<span class="parking-count">({{parkingCount}})</span></h4>
            <div class="progress">
            <el-progress :text-inside="true" :stroke-width="18" :percentage="70"></el-progress>              
            </div>
            <ul class="clearfix">
              <li class="left"><span>已占用：</span><span>0</span></li>
              <li class="right"><span>未占用：</span><span>0</span></li>
            </ul>
            <div class="income text-center">
              <span class="count">¥{{parkingIncome}}</span>总收入
            </div>
          </div>
        </div>
        <div class="col-lg-4 col-sm-6 col-xs-12">
          <div class="parking">

          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import conditionFrame from 'components/base/conditionFrame.vue'
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      selectedRegions: [],
      parkingCount: 0,
      parkingIncome: 0,
      loading: false,
      props: {
        label: 'name',
        value: 'id',
        children: 'list'
      }
    }
  },
  components: {conditionFrame},
  created () {
    this.$store.dispatch('Get_CITYLIST')
  },
  computed: {
    ...mapGetters(['cityList', 'regionLoading'])
  },
  methods: {
    // 城区选择
    regionChange (val) {
      this.checkedRegion = val.slice(-1)[0]
    },
    fetchData () {

    }
  }
}
</script>
<style lang="less" scoped>
@import "../../assets/css/grad/grad.css";
  .space{
    &-parkingCount{
      color: blue;
      margin-left: 8px;
      margin-right: 8px;
    }
    .parkingSpace{
      padding: 10px;
      cursor: pointer;
      &-box {
        width: 100%;
        // border: 1px solid #e4e4e4;
      box-shadow: 0 3px 3px #cccccc;
      background-color: #f3f3f3;        
        padding: 10px; 
      .parking-name{
        border-bottom: 1px dashed #e7e7e7;
        padding-bottom:10px;
        font-size: 16px;
      }    
      .parking-count{
        margin-left: 10px;
      } 
      .progress{
        margin-top: 10px;
        margin-bottom: 10px;
      }
      .income{
        color:#A1A0A8;
        .count{
          font-size: 40px;
        }
      }
      }
    }
  }
</style>



